import React, { Component } from 'react';
import '../bootstrap/css/bootstrap.min.css';
import './Projects.scss'

export default class Projects extends Component{
    render(){
        return(
            <section class="pb-5 pt-5 mt-0 d-flex main-header font-format container-proj" >
            <div class="container-fluid container-about">
                <div class="row  justify-content-center d-flex text-center h-100">
                    <div class="col-12 col-md-8  h-50 ">
                        <h1 class="display-2  mb-2 text-light"><strong>Projects</strong> </h1>
                        <p class="lead mb-5 text-light">
                        I have showcased my major projects below, including a few screenshots, a short description, and a link to the code.
                        </p>
                        <div class="btn-container-wrapper p-relative d-block  zindex-1">
                            <a class="btn btn-link btn-lg mt-md-3 mb-4 scroll align-self-center text-light" href="">
                                <i class="fa fa-angle-down fa-4x"></i>
                            </a>
                        </div>
                    </div>
        
                </div>
            </div>
        </section>)
    }

}